<template>
  <button class="btn">
    <span>
      <slot></slot>
    </span>
  </button>
</template>

<script>
export default {
  name: "main-button",
};
</script>

<style lang="scss" scoped>
$bg_color: lightgray;
$main_color: #000000;
$second_color: #ffffff;

.btn {
  position: relative;
  cursor: pointer;
  border: none;
  height: 50px;
  width: 150px;
  background: $bg_color;
  font-size: 15px;
  text-transform: uppercase;
  &::before {
    content: "";
    position: absolute;
    width: 0;
    height: 100%;
    background: $main_color;
    top: 0;
    left: 0;
    transition: width 0.5s ease-out;
  }
  &:hover {
    &::before {
      width: 100%;
    }
    & span {
      color: $second_color;
    }
  }
  span {
    color: $main_color;
    position: relative;
    transition: color 0.5s ease-out;
  }
}
</style>